<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="xy-scroll-view">
      <common-list
        ref="dida_list"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        @returnData="getPrizeLog($event)"
      >
        <div class="container">
          <div class="content">
            <div
              v-for="(item, index) in list.items"
              :key="index"
              :class="{ focus: ind === index }"
              class="list"
              @click="onitemClick(index)"
            >
              <div class="cyy-list">
                <div class="cyy-content">{{ item.nr }}</div>
              </div>
            </div>
          </div>
        </div>
      </common-list>
    </div>
  </div>

</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import commonList from '@/components/list/commonList.vue'
import { mapState } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'DbsxCyy',
  components: {
    headerBar,
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      ind: '', // 点击事件
      pageno: 1, // 页
      totalpageno: 0, // 总页数
      headerData: {
        title: '选择常用语',
        leftUrl: 'back',
        leftText: ' '
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },

  created() {
    this.getdbsxlist()
  },
  methods: {
    changeBgc: function(index) {
      this.ind = index
    },
    onitemClick(index) {
      this.$store.dispatch('jbrdbsx/setDBSXCYY', this.list.items[index].nr)
      this.$router.go(-1)
    },
    /** 组件中返回的数据结构
     */
    getPrizeLog(data) {
      this.list.items = data
    },
    getdbsxlist() {
      this.list.postUrl = 'jbrdbsx/getCyyListAction'
      this.list.reqData = {
        pageno: this.pageno,
        id: this.userInfo.id
      }
      this.list.dataName = 'data'
      // this.list.noDataText = '暂无更多待办事项'
    }
  }

}
</script>

<style lang="css" scoped>

</style>
